import "./NavBar.css";
function NavBar() {
  return (
    <nav
      className="navbar sticky-top navbar-expand-xl navbar-dark position-fixed w-100"
      id="navbar-example2"
    >
      <div className="container-fluid mx-auto">
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
          <ul className="nav nav-pills m-auto justify-content-center p-3">
            <li className="nav-item mx-5 p-1">
              <a className="nav-link active text-light" href="#about">
                About Me
              </a>
            </li>
            <li className="nav-item mx-5 p-1">
              <a className="nav-link text-light" href="#education">
                Education
              </a>
            </li>

            <li className="nav-item mx-5 p-1">
              <a className="nav-link text-light" href="#skills">
                Skills
              </a>
            </li>
            <li className="nav-item mx-5 p-1">
              <a className="nav-link text-light" href="#projects">
                Projects
              </a>
            </li>
            <li className="nav-item mx-5 p-1">
              <a className="nav-link text-light" href="#contact">
                Contact
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default NavBar;
